import * as Weather from "./weather";
import * as DOM from "./dom";
var location = document.getElementById("location");
function searchCity() {
    var search = document.getElementById("search");
    search.addEventListener("click", function () {
        var city = location.value.trim();
        if (!city)
            DOM.changeError();
        var GEOCODING_API = "https://api.openweathermap.org/geo/1.0/direct?q=".concat(city, "&limit=5&appid=").concat(Weather.API_KEY);
        Weather.getCityData(GEOCODING_API);
    });
}
function inputEnterKey() {
    location.addEventListener("keypress", function (event) {
        if (event.key === "Enter") {
            var city = location.value.trim();
            if (!city)
                DOM.changeError();
            var GEOCODING_API = "https://api.openweathermap.org/geo/1.0/direct?q=".concat(city, "&limit=5&appid=").concat(Weather.API_KEY);
            Weather.getCityData(GEOCODING_API);
        }
    });
}
export function loadFunctions() {
    searchCity();
    inputEnterKey();
}
import * as Interface_1 from "./interface";
export { Interface_1 as Interface };
